.overlay {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.546);
  z-index: 1000;
  transition: .3s ease-in-out;
  display: flex;
  justify-content: center;
  align-items: center;
}

.overlayFade {
  composes: overlay;
  opacity: 0;
}

.flexContainer {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100vw;
  height: 100vh;
  position: relative;
}

.imageContainer {
  width: max-content;
  max-width: 90vw;
  max-height: max-content;
  position: relative;
  transition: .2s ease-in-out;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: scroll;
  top: -50px;
}

.imageFull {
  /* composes: imageContainer; */
  width: max-content;
  max-width: 90vw;
  max-height: max-content;
  position: relative;
  transition: .2s ease-in-out;
  display: flex;
  justify-content: center;
  align-items: center;
  /* overflow: scroll; */
  width: 100vw;
  max-width: 100vw;
  height: 100vh;
  max-height: 100vh;
  position: fixed;
  /* z-index: 99999; */
  background-color: black;
}

.image {
  max-width: 90vw;
  max-height: 60vh;
  object-fit: contain;
  display: block;
  border-radius: 30px;
  overflow: scroll;
}

.imageFull > .image {
  width: 100vw;
  height: 100vh;
  max-width: 100vw;
  max-height: 100vh;
  border-radius: 0;
  overflow: scroll;
}

.imageContainer::-webkit-scrollbar {
  display: none;
}

.imageContainerFade {
  composes: imageContainer;
  opacity: 0;
}

.description {
  position: absolute;
  bottom: 30px;
  left: 30px;
  opacity: 0;
  transition: .2s ease-in-out;
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.name {
  font-size: 20px;
  font-weight: 700;
  text-shadow: 0px 0px 1px white;
  margin: 0 0 10px 0;
}

.category {
  background-color: white;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 19px;
  padding: 3px 16px 2px 16px;
  font-size: 16px;
  line-height: 32px;
  color: #391400;
  margin: 0;
}

.imageContainer:hover > .description {
  opacity: 1;
}



.buttonPrev {
  border: none;
  outline: none;
  background-color: transparent;
  background-image: url(../../images/arrowWhite.png);
  width: 50px;
  height: 50px;
  position: absolute;
  top: 50%;
  left: 10px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  cursor: pointer;
  border-radius: 10px;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.buttonNext {
  /* composes: buttonPrev; */
  border: none;
  outline: none;
  background-color: transparent;
  width: 50px;
  height: 50px;
  position: absolute;
  top: 50%;
  right: 10px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  cursor: pointer;
  border-radius: 10px;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  background-image: url(../../images/arrowWhiteRight.png);
}

.buttonPrevAnimate {
  composes: buttonPrev;
  animation: pushButton .2s ease-in-out;
}

.buttonNextAnimate {
  composes: buttonNext;
  animation: pushButton .2s ease-in-out;
}

@keyframes pushButton {
  0% {
    transform: scale(.9);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

.cardsConteiner {
  position: absolute;
  display: flex;
  /* gap: 10px; */
  bottom: 0px;
  left: 50%;
  transform: translate(-50%);
  overflow: scroll;
  max-width: 70%;
  margin: 0 auto;
  align-items: center;
  scrollbar-width: 0;
  height: 100px;
  justify-content:space-between;
}

.cardsConteiner::-webkit-scrollbar {
  display: none;
}

@media screen and (max-width: 700px) {
  .buttonPrev {
    position: absolute;
    top: initial;
    bottom: 30px;
    left: 20px;
    width: 40px;
    height: 40px;
  }

  .buttonNext {
    position: absolute;
    top: initial;
    bottom: 30px;
    right: 20px;
    width: 40px;
    height: 40px;
  }

  .imageContainer {
    max-width: 95vw;
    max-height: 60vh;
  }

  .cardsConteiner {
    width: 60%;
    height: 100px;
  }

  .description {
    bottom: 10px;
    left: 10px;
  }

  .name {
    font-size: 15px;
  }

  .category {
    font-size: 10px;
  }
}

@media screen and (max-height: 500px) {
  .imageContainer {
    max-width: 90vw;
    max-height: 60vh;
  }

  .buttonPrev {
    position: absolute;
    bottom: -17px;
    left: 45px;
    width: 40px;
    height: 40px;
  }

  .buttonNext {
    position: absolute;
    bottom: -17px;
    right: 45px;
    width: 40px;
    height: 40px;
  }

  .cardsConteiner {
    width: 80%;
  }

  .description {
    bottom: 10px;
    left: 10px;
  }

  .name {
    font-size: 15px;
  }

  .category {
    font-size: 10px;
  }
}

