.currentCard {
  min-width: 100px;
  max-width: 100px;
  min-height: 100px;
  max-height: 100px;
  transition: .2s ease-in-out;
  border-radius: 10px;
  overflow: hidden;
  cursor: pointer;
  user-select: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  background-color: rgba(0, 0, 0, 0.551);
  display: flex;
  justify-content: center;
  align-items: center;
}

.currentCardContainer {
  composes: currentCard;
  min-width: 130px;
  max-height: 130px;
  min-height: 130px;
  cursor: initial;
}

.image {
  /* overflow: scroll; */
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: bottom;
  display: block;
}

@media screen and (max-width: 700px) {
  .currentCard {
    min-width: 60px;
    /* max-width: 60px; */
    max-height: 60px;
    min-height: 60px;
  }

  .currentCardContainer {
    min-width: 90px;
    max-height: 90px;
    min-height: 90px;
    /* max-width: 90px; */
  }
}

@media screen and (max-height: 500px) {
  .currentCard {
    min-width: 60px;
    max-height: 60px;
  }

  .currentCardContainer {
    min-width: 90px;
    max-height: 90px;
    min-height: 90px;
  }
}

@media screen and (max-height: 500px) and (min-width: 700px) {
  .currentCard {
    min-width: 60px;
    width: 100%;
    /* max-width: 60px; */
    max-height: 60px;
    min-height: 60px;
  }

  .currentCardContainer {
    min-width: 90px;
    width: 100%;
    /* max-width: 90px; */
    max-height: 90px;
    min-height: 90px;
  }
}