.overlay {
  background-color: black;
  position: fixed;
  top: 0;
  left: 0;
  min-width: 100vw;
  min-height: 100vh;
  max-width: 100vw;
  max-height: 100vh;
  z-index: 999999;
  display: flex;
  align-items: center;
}

.flexContainer {
  display: flex;
  flex-direction: row;
  overflow: scroll;
  align-items: center;
  gap: 20px;
}

.image {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center;
}

.buttonsContainer {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translate(-50%);
  width: 80%;
  display: flex;
  justify-content: space-between;
  visibility: visible;
  opacity: 1;
  transition: .5s ease-in-out;
}

.buttonsContainerFade {
  composes: buttonsContainer;
  opacity: 0;
  visibility: hidden;
}